.projects-container
    padding-top: 7rem
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center

    header
        margin-top: 1rem
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: center
        gap: 1rem
        width: 90%
        .page-title
            font-family: "Roboto"
            font-size: 2.4rem
            color: rgb(25,25,45) // Marine Blue
        .header-description
            font-size: 1.5rem
            font-family: "Roboto"
            line-height: 2.4rem
            color: rgb(25,25,45) // Marine Blue
    .selector-container
        margin-top: 2rem
        height: 5rem
        .selector-box
            display: flex
            justify-content: space-between
            align-items: center
            border: 1px solid rgba(25,25,45,0.8) // Marine Blue
            border-radius: 0.6rem
            width: 25rem
            height: 5rem
            padding: 0 0.5rem 0 1rem

            .selected-value
                font-size: 1.5rem
                font-style: italic
            .selector-arrow
                width: 4rem
                height: 4rem
                transition: all 0.4s ease
            .rotate
                transform: rotate(180deg)

        .values
            height: auto
            margin-top: 0.5rem
            border: 1px solid rgba(25,25,45,0.8) // Marine Blue
            border-radius: 0.6rem
            opacity: 0
            transition: opacity 0.3s linear
            background-color: white
            .value
                font-size: 1.5rem
                padding: 1rem
                border-bottom: 1px solid rgba(25,25,45,0.1) // Marine Blue
        .expand
            opacity: 1
    .header-image
        z-index: -1
        width: 70%
    
    .projects-section
        width: 90%
        height: auto

        h2
            text-transform: uppercase
        .project
            display: flex
            flex-direction: column
            gap: 1rem

            margin: 2rem 0 2rem 0
            .name
                font-size: 2.2rem
            .label
                font-size: 1.8rem
            .description
                font-size: 1.4rem
            .project-image
                width: 100%
